import React from 'react';
import '../css/Cell.css';

const Cell = props => {
  const { cellSize, data } = props;
  var cellSizePx = cellSize + 'px';
  function handleChange(e) {
    props.onChange(e.currentTarget.value);
  }
  const cellType = props.cellInfo.type;
  return (
    <div className="Cell" style={{ width: cellSizePx, height: cellSizePx }}>
      <select
        name={props.name}
        value={data.value}
        onChange={handleChange}
        disabled={data.fixed}
        className={cellType}
      >
        <option value="0"> </option>
        {[0, 1, 2, 3, 4, 5, 6, 7, 8].map(
          val =>
            cellType == 'numbers' ? (
              <option key={val} value={val + 1}>
                {val + 1}
              </option>
            ) : cellType == 'icons' ? (
              <option
                key={val}
                value={val + 1}
                dangerouslySetInnerHTML={{ __html: props.cellInfo.iconsArray[val] }}
              />
            ) : (
              <option
                key={val}
                value={val + 1}
                dangerouslySetInnerHTML={{ __html: props.cellInfo.notesArray[val] }}
              />
            )
        )}
      </select>
    </div>
  );
};

export default Cell;



