import React from 'react';
import '../css/Block.css';
import Cell from './Cell';

const Block = props => {
  const { cellSize, blockSize } = props;
  function renderCell(data, index) {
    return (
      <Cell
        key={index}
        data={data}
        cellSize={cellSize}
        onChange={value => props.onChange(value, props.blockIndex, index)}
        cellInfo={props.cellInfo}
      />
    );
  }

  return (
    <div className="Block" style={{ width: blockSize, height: blockSize }}>
      {props.values.map(renderCell)}
    </div>
  );
};

export default Block;



